






















































import Vue, { PropType } from 'vue'
import { getProbabilitySpan, predict } from '../calc/calculations'
import { PredictionStrategy, MatchResult, MatchWithOdds, MatchPrediction } from '../types'
import odds from '../../odds-group-stage.json'

export default Vue.extend({
  name: 'Prediction',
  props: {
    strategy: {
      type: Object as PropType<PredictionStrategy>,
      required: true
    },
    dataset: {
      type: Array as PropType<MatchResult[]>,
      required: true
    }
  },
  data() {
    return {
      matches: odds as Pick<MatchWithOdds, 'odds' | 'match'>[]
    }
  },
  computed: {
    matchesWithProbabilitySpan(): MatchWithOdds[] {
      return this.matches.map((match, index) => ({
        ...match,
        round: Math.ceil((index + 1) / 12),
        probabilitySpan: getProbabilitySpan(match.odds)
      } as MatchWithOdds))
    },
    predictions(): MatchPrediction[] {
      return this.matchesWithProbabilitySpan.map((match, index, array) => ({
        ...match,
        prediction: predict({
          odds: match.odds,
          round: match.round as number,
          predicate: this.strategy.predicate,
          swapThreshold: this.strategy.swapThreshold,
          customStrategyDiff2Ratio: this.strategy.customStrategyDiff2Ratio,
          custom: this.strategy.custom,
          dataset: this.dataset,
          localDataset: array,
          predictDraws: this.strategy.predictDraws,
          goForMostProbableOutcomeAgainstOdds: this.strategy.goForMostProbableOutcomeAgainstOdds
        })
      }))
    }
  }
})
